import cn from "classnames";
import * as React from "react";
import { TextColor, TextTag, TextView, TextWeight } from "./config";
import text from "./Text.module.scss";

export type TextProps = {
  /**   */
  className?: string;
  /**   */
  view?: TextView;
  /** Html- */
  tag?: TextTag;
  /**   */
  weight?: TextWeight;
  /**  */
  children: React.ReactNode;
  /**  */
  color?: TextColor;

  fontSize?: string;
  /**  -  */
  maxLines?: number;
};

const Text: React.FC<TextProps> = ({
  className,
  view,
  tag: TextTagProp = TextTag.p,
  weight,
  children,
  color,
  maxLines,
}) => {
  return (
    <TextTagProp
      className={cn(
        text.text,
        view && text[`text_view-${view}`],
        color && text[`text_color-${color}`],
        className
      )}
      style={{
        fontWeight:
          weight === "medium" ? 500 : weight === "semiBold" ? 600 : weight,
        WebkitLineClamp: maxLines,
      }}
    >
      {children}
    </TextTagProp>
  );
};

export default Text;
